.footer {
	padding-top: 53px;
	background-color: rgba($brown, .1);
	&__inner {}
	&__top {
		display: flex;
		justify-content: space-between;
		padding-bottom: 40px;
		@include lg {
			padding-bottom: 20px; }
		@include md {
			flex-wrap: wrap;
 } }			// justify-content: center
	&__logo {
		// max-width: 156px
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		@include md {
			display: none; } }
	&-logo {
		font-size: 196px;
		@include lg {
		    font-size: 130px; }
		.icon {
			display: block;
			max-width: 100%;
			color: $brown; } }
	&-slogan {
		margin-top: 29px;
		text-align: center;
		font-size: 19px;
		@include circe;
		@include lg {
			font-size: 16px; } }
	&__menu {
		display: flex;
		@include sm {
			display: inline-block;
			column-count: 2;
			column-gap: 68px;
			width: 100%; }
		@include ms {
			column-gap: 20px; }
		&-wrap {
			text-transform: uppercase;
			padding-top: 44px;
			margin: 0 20px;
			display: flex;
			@include lg {
				padding-top: 24px; }
			@include md {
				width: 100%;
 }				// justify-content: center
			@include sm {
				display: block;
				margin: 0; } }
		&-title {
			font-weight: 700;
			// +sm
 }			// 	margin-left: 15px
		&-block {
			padding: 0 30px;
			margin: -13px 0;
			@include r(1350) {
				padding: 0 15px; }
			@include sm {
				margin: 0;
				padding: 0; } }
		&-item {
			padding: 13px 0;
			transition: color .3s;
			@include hover {
				color: $green; }
			@include sm {
				padding: 5px 0; } } }
	&__contacts {
		text-transform: uppercase;
		padding-top: 44px;
		flex-shrink: 0;
		color: $brown;
		@include lg {
			padding-top: 24px; }
		@include md {
			display: flex;
			font-size: 18px;
			padding-top: 40px; }
		@include ms {
			flex-wrap: wrap; }
		&-item {
			@include ms {
				width: 100%; }
			&:not(:first-child) {
				margin-top: 50px;
				@include md {
					margin-top: 0;
					margin-left: 50px; }
				@include ms {
					margin-left: 0;
					margin-top: 20px; } }
			span {
				font-size: 32px;
				@include r(1502) {
					font-size: 24px; }
				@include lg {
					font-size: 20px; }
				@include md {
					font-size: 25px; } } } }
	&__bottom {
		padding: 19px 0 26px;
		background-color: $brown;
		@include md {
			padding: 40px 0; }
		&-wrap {
			display: flex;
			justify-content: space-between;
			align-items: center;
			flex-wrap: wrap;
			@include ms {
				justify-content: center; } } }
	&__copyright {
		color: $white;
		@include md {
			display: none; } }
	&__social {
		display: flex;
		align-items: center;
		margin: 0 15px;
		@include sm {
			flex-wrap: wrap;
			margin: 0;
			justify-content: center; } }
	&__stores {
		@include r(847) {
			margin-top: 35px; } }
	&__agree {
		display: none;
		@include md {
			display: block;
			width: 100%;
			margin-top: 30px; }
		&-link {
			display: block;
			text-decoration: underline;
			color: $brown;
			font-size: 16px; } } }

.subscribe {
	display: block;
	color: $white;
	text-decoration: none;
	text-transform: uppercase;
	transition: color .3s;
	font-size: 22px;
	@include hover {
		color: $orange; }
	@include lg {
		font-size: 17px; }
	@include md {
		font-size: 18px; }
	@include r(489) {
		width: 100%;
		margin-top: 30px; }
	&:not(:first-child) {
		margin-left: 41px;
		@include lg {
			margin-left: 20px; }
		@include r(489) {
			margin-left: 0;
			text-align: center; } }
	.header__social & {
		@include f(16px, 500);
		margin-right: 20px;
		@include lg {
			font-size: 12px;
			margin-right: 10px; } } }

.social {
	display: flex;
	align-items: center;
	&__item {
		font-size: 35px;
		color: $white;
		transition: color .3s;
		@include hover {
			color: $orange; }
		@include lg {
			font-size: 27px; }
		@include md {
			font-size: 40px; }
		&:not(:first-child) {
			margin-left: 32px;
			@include lg {
				margin-left: 20px; } }
		svg {
			display: block;
			max-width: 100%; }
		.header__social & {
			font-size: 27px;
			flex-shrink: 0;
			&:not(:first-child) {
				margin-left: 20px;
				@include lg {
					margin-left: 10px; } }
			@include lg {
				font-size: 18px; } } } }

.stores {
	display: flex;
	justify-content: space-between;
	align-items: center;
	&__item {
		max-width: 150px;
		@include lg {
			max-width: 100px; }
		@include md {
			max-width: 160px; }
		&:not(:first-child) {
			margin-left: 60px;
			@include lg {
				margin-left: 30px; } }
		img {
			display: block;
			max-width: 100%;
			width: 100%; } } }
