.form {
    &__btn {
        margin-top: 10px;
        .form_main & {
            margin-top: 35px; } } }
.form-group {
	margin-top: 15px;
	input, textarea {
		width: 100%;
		@include roboto;
		@include f(20px, 400);
		color: $black-opa;
		padding: 16px 20px;
		border-radius: 5px;
		@include placeholder {
			@include roboto;
			@include f(20px, 400);
			color: $black-opa;
			@include lg {
				font-size: 14px; }
			@include sm {
				font-size: 12px; } }
		@include lg {
			font-size: 14px;
			padding: 10px; }
		@include sm {
			font-size: 12px; }
		&:focus {
			@include placeholder {
				font-size: 0; } } }
	textarea {
		resize: none;
		min-height: 180px;
		height: 100%;
		border: none;
		@include lg {
			min-height: 100px; } }
	.form_main & {
		input, textarea {
			color: $black;
			@include placeholder {
				color: $black; } } } }
