.title {
	@include circe;
	@include f(40px, 700);
	text-transform: uppercase;
	color: $brown;
	@include lg {
	    font-size: 30px; }
	&_lg {
		font-size: 53px;
		letter-spacing: -0.025em;
		@include ms {
			font-size: 24px; } }
	&_m {
		font-size: 30px;
		line-height: 1.5;
		@include lg {
			font-size: 21px; }
		@include sm {
			font-size: 16px; } }
	&_s {
		@include f(22px, 700, 1);
		color: $white;
		@include lg {
			font-size: 18px; } } }

.description {
    @include roboto;
    @include f(20px, 400);
    color: $black;
    @include lg {
        font-size: 14px; } }
