.card {
	padding-bottom: 120px;
	@include md {
		padding-top: 131px; }
	&__inner {}
	&__img {
		@include cover;
		height: 872px;
		&-bg.bg_pink {
			background-color: $pinkLight; }
		&-bg.bg_pow {
			background-color: $powderDark; }
		&-bg.bg_green {
			background-color: $greenLight; } }
	&__description {
		display: flex;
		&-bg.bg_pink {
			background-color: rgba($pinkLight, .3); }
		&-bg.bg_pow {
			background-color: rgba($powderDark, .3); }
		&-bg.bg_green {
			background-color: rgba($greenLight, .3); } }
	&__left {
		@include flex-size(60%); }
	&__right {
		@include flex-size(40%);
		&_single {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			width: 100%; } }
	&__title-wrap {
		padding: 50px 20px 30px 0; }
	&__title {}
	&__name {
		padding-top: 15px;
		&.title {
			color: $black;
			font-weight: 400;
			line-height: 1.2; } }
	&__specifications {
		background-color: $white;
		width: 100%;
		height: 100%;
		padding: 54px 40px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-start;
		@include lg {
			padding: 30px 20px; } }
	&__specification {
		// width: 50%
		&:not(:last-child) {
			margin-bottom: 20px; }
		&.specification_wide {
 } }			// width: 100%
	&__recipe {
		padding: 30px 0 80px; }
	&__basket {
		justify-content: space-between;
		padding-bottom: 30px;
		padding-right: 35px; }
	&__btn {
		&_padding {
			padding-top: 50px; } } }

.specification {
	display: flex;
	align-items: center;
	font-size: 20px;
	padding: 0 10px;
	&__icon {
		width: 44px;
		height: 44px;
		@include contain;
		flex-shrink: 0;
		margin-right: 25px;
		@include lg {
			width: 35px;
			height: 35px; }
		.main-dish__specification & {
			margin-right: 10px; } }
	&__aside {
		font-size: 14px;
		text-align: center; }
	@include lg {
		font-size: 16px; } }

.recipe {
	display: flex;
	&__header {
		background-color: $lightGray;
		padding: 10px 40px;
		.card__right & {
			background-color: $grayMid; } }
	&__body {
		margin-top: 35px;
		display: flex; }
	&__list {
		padding-left: 40px;
		min-width: 275px; } }
