.hero {
	overflow: hidden;
	@include md {
		padding-bottom: 50px;
		padding-top: 136px; }
	@include ms {
		padding-top: 182px; }
	&__inner {
		position: relative;
		@include md {
			padding: 0; } }
	&__prev-arrow,
	&__next-arrow {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		color: $white;
		z-index: 2;
		cursor: pointer;
		user-select: none;
		border-radius: 50%;
		transition: background-color .3s;
		@include hover {
			background-color: rgba($black, .2); }
		@include md {
			display: none; }
		.icon {
			display: block;
			font-size: 57px; } }
	&__prev-arrow {
		left: -36px;
		@include r(1760) {
			left: 5px; } }
	&__next-arrow {
		right: -36px;
		@include r(1760) {
			right: 5px; } }
	&__dot {
		width: 10px;
		height: 10px;
		border-radius: 50%;
		background-color: $white;
		transition: background-color .3s;
		@include md {
			background-color: $gray; }
		.slick-initialized li.slick-active & {
			background-color: $green; } }
	.slick-list {
		overflow: visible;
		@include md {
			overflow: hidden; } }
	.slick-dots {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 26px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		li {
			line-height: 0;
			&:not(:first-child) {
				margin-left: 18px; } }
		@include md {
			bottom: -30px; } } }

.hero-slider {
	&:before,
	&:after {
		content: '';
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($brown, .6);
		z-index: 1;
		@include md {
			display: none; } }
	&:before {
		left: 100%; }
	&:after {
		right: 100%; }
	&__bg {
		padding: 109px 0 88px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		@include md {
			padding: 0 0 230px 0; }
		@include ms {
			padding-bottom: 150px; } }
	&__text {
		opacity: 0;
		visibility: hidden;
		transition: opacity .3s, visibility .3s;
		@include md {
			text-align: center; }
		.slick-active & {
			opacity: 1;
			visibility: visible; } }
	&__title {
		padding: 27px 106px 21px 98px;
		background-color: rgba($white, .6);
		max-width: 1032px;
		@include md {
			padding: 36px 20px 23px; }
		.hero-slider__btn {
			display: none;
			@include md {
				display: block; }
			.btn {
				min-width: 195px; } } }
	&__content {
		padding: 25px 122px 30px 102px;
		max-width: 535px;
		background-color: rgba($brown, .6);
		font-size: 22px;
		font-weight: 500;
		color: $white;
		@include md {
			display: none; } }
	&__btn {
		margin-top: 30px;
		@include md {
			margin-top: 20px; } } }
