.menu {
	overflow: hidden;
	&__inner {
		position: relative;
		@include sm {
			padding: 0; } }
	&__prev-arrow,
	&__next-arrow {
		position: absolute;
		z-index: 2;
		cursor: pointer;
		user-select: none;
		top: 72px;
		border-radius: 50%;
		transition: background-color .3s;
		@include hover {
			background-color: rgba($black, .2); }
		@include lg {
			top: 40px; }
		.icon {
			display: block;
			font-size: 57px;
			@include lg {
				font-size: 40px; }
			@include xs {
				font-size: 30px; } } }
	&__prev-arrow {
		left: 71px;
		@include sm {
			left: 34px; }
		@include xs {
			left: 10px; } }
	&__next-arrow {
		right: 550px;
		@include xxl {
			right: 470px; }
		@include lg {
			right: 400px; }
		@include md {
			right: 71px; }
		@include sm {
			right: 34px; }
		@include xs {
			right: 10px; } }
	.slick-list {
		overflow: visible; } }

.menu-slider {
	&:before,
	&:after {
		content: '';
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($white, .5);
		z-index: 1;
		@include sm {
			display: none; } }
	&:before {
		left: 100%; }
	&:after {
		right: 100%; }
	&__slide {
		height: 850px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		@include lg {
			height: 700px; }
		@include sm {
			height: 580px; }
		&-wrap {
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			opacity: 0;
			visibility: hidden;
			transition: opacity .3s, visibility .3s;
			.slick-active & {
				opacity: 1;
				visibility: visible; } } }
	&__top {
		padding: 51px 43px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		background-color: rgba($white, .5);
		line-height: 1.1;
		color: $brown;
		@include lg {
			padding: 30px; }
		@include sm {
			background-color: $powder;
 }			// display: none // !!!!!!!!!!!!!!!!!!!!!!!!!!
		&.menu-slider__top_display {
			display: none;
			@include sm {
				display: block; } } }
	&__bottom {
		@include sm {
 }			// display: none //!!!!!!!!!!!!!!!!!!!!!!
		&.menu-slider__bottom_display {
			display: none;
			@include sm {
				display: block; } } }
	&__text {
		padding: 0 170px 0 73px;
		max-width: 1150px;
		width: 100%;
		@include xxl {
			padding-right: 100px; }
		@include lg {
			padding-left: 50px;
			padding-right: 120px; }
		@include sm {
			padding: 0 80px;
			text-align: center; }
		@include xs {
			padding: 0 30px; } }
	&__title {
		font-size: 45px;
		font-weight: 700;
		@include circe;
		@include xxl {
			font-size: 37px; }
		@include lg {
			font-size: 29px; }
		span {
			font-weight: 900; } }
	&__subtitle {
		margin-top: 8px;
		font-weight: 500;
		font-size: 24px;
		@include xxl {
			font-size: 20px; }
		@include lg {
			font-size: 16px; } }
	&__btn {
		flex-shrink: 0;
		@include md {
			display: none; }
		&_mob {
			display: none;
			@include md {
				display: block;
				text-align: center;
				padding-top: 35px; }
			@include sm {
				padding-top: 175px; }
			@include ms {
				padding-top: 140px; } } }
	&__list {} }


.menu-list {
	display: flex;
	// align-items: center
	background-color: rgba($black, .35);
	@include sm {
		flex-wrap: wrap;
		// background-color: rgba($gray, .4)
		position: relative;
		top: 140px; }
	@include ms {
		top: 118px; }
	&__item {
		width: 33.33%;
		position: relative;
		color: $white;
		transition: background-color .3s;
		@include hover {
			.icon {
				background-color: rgba($black, .2); }
			.menu-ingredients {
				opacity: 1;
				visibility: visible; }
			.menu-list__ingredients-wrap {
				opacity: 1;
				visibility: visible; } }
		@include sm {
			width: 100%;
			position: static; }
		.icon {
			display: block;
			margin-left: 35px;
			font-size: 55px;
			border-radius: 50%;
			transition: background-color .3s;
			position: absolute;
			right: 30px;
			top: 50%;
			transform: translateY(-50%);
			@include lg {
				font-size: 42px;
				margin-left: 20px; }
			@include md {
				font-size: 25px;
				right: 10px; }
			@include sm {
				font-size: 40px; }
			// +xxl
 }			//     margin-left: 25px
		&_green {
			@include hover {
				background-color: rgba($green, .5); }
			@include md {
				a {
					&.is-active {
						background-color: rgba($green, .4); } } } }
		&_orange {
			@include hover {
				background-color: rgba($orange, .5); }
			@include md {
				a {
					&.is-active {
						background-color: rgba($orange, .4); } } } }
		&_pink {
			@include hover {
				background-color: rgba($pink, .5); }
			@include md {
				a {
					&.is-active {
						background-color: rgba($pink, .4); } } } } }
	&__link {
		padding: 38px 90px 38px 30px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		// justify-content: center
		text-transform: uppercase;
		font-size: 30px;
		font-weight: 700;
		@include circe;
		@include xxl {
			font-size: 25px; }
		@include lg {
			font-size: 18px;
			padding: 25px 75px 25px 25px; }
		@include md {
			padding: 15px 60px 15px 15px;
			font-size: 16px; }
		@include sm {
			font-size: 28px;
			position: relative;
			padding-right: 70px; }
		@include ms {
			font-size: 20px; } } }

.menu-ingredients {
	padding: 46px 45px 41px 76px;
	overflow: auto;
	color: $brown;
	font-size: 32px;
	font-weight: 700;
	text-transform: uppercase;
	line-height: 1.1;
	scrollbar-color: #c7c7cc #efeff4;
	scrollbar-width: thin;
	@include xxl {
		font-size: 26px;
		max-height: 450px; }
	@include lg {
		font-size: 20px; }
	@include md {
		padding: 25px 40px 70px 25px; }
	&::-webkit-scrollbar {
		width: 6px;
		height: 6px;
		@include sm {
			display: none; } }
	&::-webkit-scrollbar-track {
		background: #efeff4;
		@include sm {
			display: none; } }
	&::-webkit-scrollbar-thumb {
		background: #c7c7cc;
		@include sm {
			display: none; } }
	&::-webkit-scrollbar-thumb:hover {
		background: #b1b1b2;
		@include sm {
			display: none; } }
	&__item {
		transition: color .3s;
		&:not(:first-child) {
			margin-top: 20px; }
		.menu-list__item_green & {
			@include hover {
				color: $green; } }
		.menu-list__item_pink & {
			@include hover {
				color: $pink; } }
		.menu-list__item_orange & {
			@include hover {
				color: $orange; } } }
	// &.is-active
	// 	+md
	// 		opacity: 1
 }	// 		visibility: visible

.menu-list__ingredients-wrap {
	overflow: auto;
	position: absolute;
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s, visibility .3s;
	bottom: 100%;
	width: 100%;
	left: 0;
	// padding: 46px 45px 41px 76px
	background-color: rgba($white, .5);
	max-height: 500px;
	// position: relative
	// opacity: 0
	// visibility: hidden
	// transition: opacity .2s, visibility .2s
	// z-index: -5
	// transform: translateX(-100%)
	&.is-active {
		@include md {
			opacity: 1;
			visibility: visible;
			z-index: 2;
 } }			// transform: translateX(0)

	.slick-list {
		overflow: hidden; }
	.slick-dots {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 30px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		li {
			line-height: 0;
			&:not(:first-child) {
				margin-left: 18px; } } } }

.slick-slider {
    opacity: 0;
    visibility: hidden;
    transition: opacity .2s, visibility .2s;
    &.slick-initialized {
        visibility: visible;
        opacity: 1; } }
