.header {
	@include md {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 10;
		width: 100%; }
	&__top {
		background-color: $green;
		color: $white;
		@include md {
			padding: 12px 0; } }
	&__inner {
		position: relative;
		display: flex;
		@include md {
			justify-content: space-between;
			align-items: center; } }
	&__left,
	&__right {
		width: 50%;
		display: flex;
		@include md {
			width: auto; } }
	&__left {
		padding-right: 100px;
		@include lg {
			padding-right: 80px; }
		@include md {
			display: none; } }
	&__right {
		padding-left: 100px;
		justify-content: flex-end;
		@include lg {
			padding-left: 80px; }
		@include md {
			padding: 0;
			// justify-content: center
			width: 100%; } }
	&__title {
		display: none;
		@include md {
			display: block;
			font-size: 43px;
			// width: 115px
			a {
				display: flex; }
			// img
			// 	display: block
 } }			// 	width: 100%
	&__logo {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		background-color: $white;
		width: 200px;
		padding: 10px 30px;
		z-index: 1; // нижнюю часть ссылки перекрывает меню без z-index
		@include lg {
			width: 150px; }
		img {
			display: block;
			max-width: 100%; }
		a {
			display: block; }
		@include md {
			display: none; } }
	&__phone {
		margin-left: 15px;
		display: flex;
		align-items: center;
		span {
			font-size: 30px;
			@include xxl {
				font-size: 25px; } } }
	&__city {
		height: 100%; }
	&__bottom {
		padding: 73px 0 31px;
		color: $green;
		@include lg {
			padding: 40px 0 15px; }
		@include md {
			padding: 15px 0;
			background-color: $white; } }
	&__social {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 40px;
		@include xxl {
			flex-wrap: wrap;
			justify-content: center;
			padding: 0 20px; }
		@include lg {
			padding: 0 10px; }
		@include md {
			display: none; } } }

.main-menu {
	display: flex;
	align-items: center;
	letter-spacing: 0.05em;
	font-weight: 500;
	text-transform: uppercase;
	&__item {
		position: relative;
		padding: 27px 0 10px;
		display: flex;
		align-items: center;
		line-height: 1;
		white-space: nowrap;
		transition: color .3s;
		@include hover {
			color: $orange; }
		@include lg {
			padding: 18px 0 8px; }
		&:not(:first-child) {
			margin-left: 60px;
			@include xxl {
				margin-left: 40px; }
			@include lg {
				margin-left: 25px; } }
		&:hover {
			.main-menu__submenu {
				opacity: 1;
				visibility: visible; } }
		&_submenu {
			z-index: 1; } }
	&__link {
		display: flex;
		align-items: center; }
	&__arrow {
		display: block;
		font-size: 11px;
		margin-left: 15px;
		@include lg {
			font-size: 6px;
			margin-left: 8px; }
		.icon {
			display: block; } }
	&__submenu {
		position: absolute;
		top: 0;
		left: -21px;
		padding: 105px 39px 41px 21px;
		background-color: rgba($lightGray, .8);
		opacity: 0;
		visibility: hidden;
		z-index: -1;
		font-weight: 700;
		letter-spacing: 0.025em;
		color: $brown;
		transition: opacity .3s, visibility .3s;
		@include lg {
			padding: 70px 21px 30px; }
		&-item {
			transition: color .3s;
			@include hover {
				color: $green; }
			&:not(:first-child) {
				margin-top: 25px; } }
		&-link {
			display: block;
			white-space: nowrap; } } }

.basket {
	display: flex;
	align-items: center;
	&__text {
		display: flex;
		align-items: baseline;
		font-weight: 500;
		line-height: .9;
		@include ms {
			width: 100%;
			padding-bottom: 20px;
			justify-content: center; } }
	&__title {
		text-transform: uppercase;
		@include md {
			font-size: 15px; } }
	&__sum {
		margin-left: 12px;
		font-size: 40px;
		font-weight: 700;
		@include xxl {
			font-size: 34px; }
		@include lg {
			font-size: 30px; }
		.card__basket & {
			font-size: 35px;
			margin: 0; }
		.choice__basket & {
			font-size: 50px; } }
	&__currency {
		margin-left: 10px;
		.card__basket & {
			font-size: 25px; }
		.choice__basket & {
			font-size: 28px; } }
	&__weight {
		margin-left: 40px;
		font-size: 20px;
		color: $black; }
	&__btn {
		margin-left: 14px;
		@include md {
			margin-left: 40px; }
		@include ms {
			margin-left: 0; } }
	@include ms {
		flex-wrap: wrap;
		justify-content: center;
		width: 100%; }
	&.card__basket {
		color: $brown; } }

.city {
    position: relative;
    display: flex;
    align-items: center;
    z-index: 1;
    padding-right: 18px;
    height: 100%;
    &:hover {
        .city__list {
            opacity: 1;
            visibility: visible; } }
    &__current {
        z-index: 1; }
    &__arrow {
        font-size: 11px;
        margin-left: 20px;
        z-index: 1;
        @include lg {
            font-size: 6px;
            margin-left: 8px; }
        .icon {
            display: block; } }
    &__list {
        position: absolute;
        top: 0;
        left: -23px;
        right: 0;
        padding: 83px 23px 36px;
        background-color: rgba($black,.4);
        opacity: 0;
        visibility: hidden;
        transition: opacity .3s, visibility .3s;
        @include lg {
            padding: 60px 23px 20px; } }
    &__item {
        transition: color .3s;
        @include hover {
            color: $orange; }
        a {
            display: block; } } }
.account {
	padding: 0 27px;
	display: flex;
	align-items: center;
	@include lg {
		padding: 0 15px; }
	@include md {
		flex-wrap: wrap;
		justify-content: center; }
	&-wrap {
		padding: 25px 0;
		@include lg {
			padding: 15px 0; }
		&_bg {
			background-color: $pink; }
		@include md {
			display: none; }
		.mob-menu & {
			display: flex;
			justify-content: center;
			align-items: center; } }
	&__icon {
		margin-right: 20px;
		font-size: 40px;
		@include xxl {
			font-size: 36px; }
		@include lg {
			font-size: 30px;
			margin-right: 10px; }
		@include md {
			margin: 0;
			padding: 0 20px; }
		.icon {
			display: block; } }
	&__text {
		width: 310px;
		padding-right: 20px;
		font-size: 20px;
		@include xxl {
			font-size: 18px;
			width: 290px; }
		@include lg {
			font-size: 16px;
			width: 250px; }
		@include md {
			width: 100%;
			text-align: center;
			padding: 10px 0; } }
	&__points {
		display: flex;
		flex-direction: column;
		align-items: center;
		line-height: 1;
		&-number {
			font-size: 30px;
			font-weight: 700;
			@include lg {
				font-size: 24px; } }
		&-text {
			font-size: 16px;
			@include lg {
				font-size: 14px;


 } } } }				// SMALL DEVICES

.header__toggle {
	cursor: pointer;
	display: none;
	align-items: center;
	width: 31px;
	height: 30px;
	@include md {
		display: flex; } }

.header__toggle-icon {
	position: relative;
	width: 100%;
	height: 4px;
	background-color: $white;
	&:before, &:after {
		content: '';
		position: absolute;
		width: 100%;
		height: 4px;
		left: 0;
		background-color: $white;
		transition: transform .3s; }
	&:before {
		top: -12px; }
	&:after {
		bottom: -12px; }
	&.is-open {
		background-color: transparent;
		&:before {
			@include rotate(45);
			top: 0;
			background-color: $white; }
		&:after {
			@include rotate(-45);
			bottom: 0;
			background-color: $white; } } }

.mob-menu {
	visibility: hidden;
	opacity: 0;
	position: fixed;
	top: 0;
	left: 0;
	max-width: 315px;
	width: 100%;
	background-color: rgba($brown, .9);
	color: $white;
	font-size: 20px;
	padding: 15px 0;
	z-index: 15;
	transition: visibility .3s, opacity .3s;
	@include ms {
		top: 67px; }
	&__list {
		text-transform: uppercase;
		border-bottom: 1px solid $mob-border;
		padding-bottom: 30px; }
	&__item {
		position: relative;
		&.is-active {
			background-color: $brown;
			&:before {
				content: '';
				position: absolute;
				width: 9px;
				height: 100%;
				top: 0;
				left: 0;
				background-color: $green;
				color: $green; } } }
	&__link {
		display: block;
		padding: 14px 0 14px 40px; }
	&__submenu {
		padding-left: 40px;
		&-link {
			display: block;
			position: relative;
			padding: 12px 0 12px 20px;
			&.is-active {
				background-color: $brown;
				&:before {
					content: '';
					position: absolute;
					width: 9px;
					height: 100%;
					top: 0;
					left: 0;
					background-color: $green;
					color: $green; } } } }
	&__contacts {
		padding: 25px 0 25px 40px;
		border-bottom: 1px solid $mob-border;
		&-wrap {
			display: flex;
			align-items: center; } }
	&__arr {
		width: 24px;
		height: 24px;
		@include cover;
		margin-right: 22px; }
	&__city {
		font-size: 22px; }
	&__phone {
		font-size: 18px;
		margin-left: 44px;
		& span {
			font-size: 30px; } }
	&__buttons {
		padding: 25px 0 25px 40px;
		border-bottom: 1px solid $mob-border;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		& .btn {
			padding: 12px 35px;
			font-size: 16px;
			&:not(:first-child) {
				margin-top: 25px; } } }
	&__socials {
		color: $white;
		font-size: 26px;
		line-height: 1;
		padding: 25px 0 20px 40px;
		display: flex;
		align-items: center;
		border-bottom: 1px solid $mob-border;
		&-icon {
			display: block;
			transition: color .3s;
			&:not(:first-child) {
				margin-left: 20px; }
			@include hover {
				color: $orange; } }
		.icon-vk-m {
			font-size: 34px; } }
	&__aside {
		padding: 25px 0 25px 40px;
		font-size: 20px;
		text-transform: uppercase;
		a {
			display: block;
			font-size: 20px;
			text-transform: uppercase;
			padding: 10px 0; } }
	@include ms {
		max-width: none; }
	&.is-open {
		@include md {
			visibility: visible;
			opacity: 1;
			overflow-y: scroll;
			height: 100vh; } } }
