.stock {
	padding: 40px 0 0 0;
	background-color: $green-bg;
	@include md {
		padding: 28px 0 30px; }
	&__inner {
		&:not(:first-child) {
			padding: 0; }
		.slick-dots {
			display: flex;
			position: absolute;
			bottom: -24px;
			left: 50%;
			transform: translateX(-50%);
			li {
				&:not(:first-child) {
					margin-left: 20px; } } } }
	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include md {
			justify-content: center; } }
	&__list {
		display: flex;
		margin-top: 35px;
		@include md {
			margin-top: 25px;
			display: block;
			position: relative; } }
	&__link {
		color: $black;
		width: 25%;
		// cursor: pointer
		@include md {
			width: 100%;
			padding: 0 20px; }
		@include xs {
			padding: 0; } }
	&__btn {
		@include md {
			display: none; }
		&_mob {
			display: none;
			@include md {
				display: block;
				text-align: center;
				padding-top: 50px; } } } }

.stock-item {
	display: block;
	// max-width: 400px
	font-size: 25px;
	height: 100%;
	background-color: rgba(0, 0, 0, 0);
	transition: background-color .3s;
	cursor: pointer;
	@include hover {
		background-color: rgba(0, 0, 0, 0.1);
		.stock-item__date {
			color: $orange; } }
	@include lg {
		font-size: 18px; }
	@include md {
		max-width: 480px;
		margin: 0 auto; }
	@include xs {
		max-width: none; }
	&__img {
		// max-width: 400px
		width: 100%;
		height: 0;
		padding-bottom: 94%;
		@include cover;
		@include md {
			max-width: none; } }
	&__text {
		padding: 20px 12.5% 10% 15%;
		@include md {
			padding: 20px 10px; }
		@include xs {
			padding: 20px 50px; } }
	&__date {
		font-weight: 700;
		transition: color .3s;
		@include md {
			font-size: 22px; } } }
