  background-color: transparent;
}

.header__toggle-icon.is-open:before {
  transform: rotate(45deg);
  top: 0;
  background-color: #fff;
}

.header__toggle-icon.is-open:after {
  transform: rotate(-45deg);
  bottom: 0;
  background-color: #fff;
}

