  font-size: 22px;
  user-select: none;
}

.check input {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
}

.check input:checked + .check__checkmark:after {
  background-color: #6aa943;
}

.check__checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 73px;
  width: 73px;
  border-radius: 50%;
  background-color: #fff;
  border: 1px solid #483d3c;
}

.check__checkmark:after {
  content: "";
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 52px;
  width: 52px;
  border-radius: 50%;
  background-color: #e0e0e0;
  transition: background-color .2s;
}

