.review {
	padding: 40px 0;
	&__inner {
		@include md {
			padding: 0; } }
	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include md {
			justify-content: center; } }
	&__list {
		display: flex;
		margin: 35px -40px 0;
		@include lg {
			margin: 35px -20px 0; }
		@include md {
			margin: 35px 0 0;
			display: block;
			position: relative;
			padding-bottom: 50px; }
		@include xs {
			max-width: 270px;
			margin: 35px auto 0; }
		.slick-list {
			@include xs {
				overflow: visible; } }
		.slick-dots {
			display: flex;
			position: absolute;
			bottom: 0px;
			left: 50%;
			transform: translateX(-50%);
			li {
				&:not(:first-child) {
					margin-left: 20px; } } } }

	&__item-wrap {
		padding: 0 40px;
		width: 25%;
		max-width: 400px;
		@include lg {
			padding: 0 20px; } }
	&__btn {
		@include md {
			display: none; }
		&_mob {
			display: none;
			@include md {
				display: block;
				text-align: center;
				padding-top: 30px; } } } }

.review-item {
	width: 100%;
	font-size: 25px;
	@include lg {
		font-size: 20px; }
	&__img-wrap {
		display: block;
		border: 10px solid $coffee;
		height: 0;
		padding-bottom: 94%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%; }
	&__text {
		margin-top: 25px; }
	&__link-wrap {
		line-height: 1.2;
		&:not(:first-child) {
			margin-top: 5px; }
		&:last-child {
			margin-top: 10px; } }
	&__link {
		transition: color .3s;
		@include hover {
			color: $orange; }
		&_name {
			font-weight: 500; }
		&_insta {
			color: $blue;
			text-decoration: none; } } }
