.delivery-info {
	text-align: center;
	padding: 64px 0 32px;
	color: $brown;
	@include md {
		padding: 10px 0 36px; }
	&__title.title {
		color: $green; }
	&__wrap {
		margin: 25px -45px 0;
		display: flex;
		justify-content: center;
		@include lg {
			margin: 25px -25px 0; }
		@include md {
			flex-wrap: wrap;
			margin: 25px 0 0; } }
	&__item {
		padding: 0 45px;
		max-width: 400px;
		width: 33.3%;
		@include lg {
			padding: 0 25px; }
		@include md {
			width: 100%;
			max-width: none;
			padding: 0;
			&:not(:first-child) {
				margin-top: 20px; } } }
	&__btn {
		text-align: center;
		margin-top: 30px;
		a {
			min-width: 170px; }
		@include md {
			.btn {
				background-color: $pink; } } } }


.delivery-item {
	text-align: center;
	width: 100%;
	// &__img-wrap
	//     display: flex
	//     justify-content: center
	//     align-items: flex-end
	//     min-height: 200px
	&__text {
		margin-top: 30px;
		@include f(27px, 700);
		@include lg {
			font-size: 20px; }
		@include ms {
			font-size: 16px; }
		span {
			text-transform: uppercase;
			display: block; }
		@include md {
			margin-left: 30px;
			margin-top: 0;
			max-width: 300px; }
		@include ms {
			max-width: 250px; } }
	& .icon {
		font-size: 11em;
		@include md {
			flex-shrink: 0;
			min-width: 165px; }
		@include ms {
			min-width: 135px; } }
	& .icon-info-menu {
		color: $orange;
		@include md {
			font-size: 10em; }
		@include ms {
			font-size: 9em; } }
	& .icon-info-box {
		color: $green;
		@include md {
			font-size: 11em; }
		@include ms {
			font-size: 9em; } }
	& .icon-info-dish {
		color: $pink;
		@include md {
			font-size: 9em; }
		@include ms {
			font-size: 7em; } }
	@include md {
		display: flex;
		justify-content: center;
		align-items: center; } }
